
<template>
    <div></div>
    
</template>

<script>
export default {
    beforeCreate(){
        console.log("I am being called, Ho-Me")
        // This is to check whether the access token is still valid
        fetch('http://127.0.0.1:8000/jov/api/userquery/isLoggedin/',{
        method : 'POST',
        headers: {
            'Content-Type' : 'application/json',
            'Authorization' : 'Bearer '+ this.$store.state.user.access
            },
        }).then((response)=>{
            if (response.ok){
            this.$store.state.isLoggedIn = true
            console.log("Redirecting to HOMe-OPTIONS")
            this.$router.push('/home')
            } else {
            this.$store.state.isLoggedIn = false
            console.log("REdirecting To MEnu")
            this.$router.push('/menu')
            }
            return response.json()
        }).then((data)=>{
            console.log("The response is: ", data)
            this.$store.state.isAdmin = data.is_Admin
        }).catch((error)=>{
            console.log("THis is the error: ", error.message)
        })
    },
}
</script>